@import "styles/shared";

body {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding-top: 20px;
  background-color: $white;
  color: $black;
  line-height: 1.5;
  font-family: $primary-font-stack;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

p {
  margin-top: 0;
}
